.page {
  color: $main-color-text;
  font-family: Poppins, sans-serif;
  font-size: 16px;
  line-height: 140%;
  font-weight: 400;
  cursor: $cursor-page;

  &__body {
    margin: 0;
    min-width: 320px;
    background-color: $page-background-color;

    &.lock {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    opacity: 0;
    transform: translateX(-100%);

    @include add-transition;

    transition-property: all;
    pointer-events: none;

    &.open-menu {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
      z-index: 1;

      overflow: hidden;
    }
  }

  a:hover, button:hover {
    cursor: $cursor-menu;
  }
}
